.fb-count-down-wrapper {

	.fb-count-down-item {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		text-align: center;
		position: relative;

		&:last-child {
			&:before {
				display: none;
			}
		}
	}

	.fb-count-down-item:before{
		position: absolute;
		right: 0;
		top: 50%;
		transform: translateY(-50%);
	}
}
